export default {
  loading: false,
  asyncData() {
    return new Promise(resolve => {
      setTimeout(function() {
        resolve({})
      }, 1000)
    })
  },
  mounted() {
    setTimeout(() => {
      // Extend loader for an additional 5s
      this.$nuxt.$loading.finish()
    }, 5000)
  },
  methods: {
    goToFinal() {
      // Start loader immediately
      this.$nuxt.$loading.start()
      // Actually change route 5s later
      setTimeout(() => {
        this.$router.push('/test')
      }, 5000)
    }
  }
}
